<template>
	<div>
	 <el-row :gutter="12">
      <el-col :span="12">
        <el-card shadow="hover">
          <span id="block"><b>Mood</b></span>
          <div id="MoodIcons">
            <img src="@/assets/emoji/emoji_supergood.png">
            <img src="@/assets/emoji/emoji_verygood.png">
            <img src="@/assets/emoji/emoji_good.png">
            <img src="@/assets/emoji/emoji_neutral.png">
            <img src="@/assets/emoji/emoji_bad.png">
            <img src="@/assets/emoji/emoji_verybad.png">
            <img src="@/assets/emoji/emoji_superbad.png">
          </div>
          <div id="MoodChart"></div>
          <div class="MoodWords" v-for="mood in mood_words">
            {{mood[0]}}<span>{{mood[1]}}</span> 

          </div>
        </el-card>
        <!-- <el-card shadow="hover" class="second">
          <span><b>Keywords</b></span>
          <div id="keywords">
            
          </div>
        </el-card>
 -->      </el-col>
      <el-col :span="12">
        <el-card shadow="hover">
          <span><b>Entries</b></span>
          <div id="EntryChart"></div>
        </el-card>
        <!-- <el-card shadow="hover" class="second">
          <span><b>Cognitive Distortions</b></span>
        </el-card>
 -->      </el-col>
    </el-row>
	</div>
</template>

<script>
  import axios from "axios"
  function descend(x,y) {
    return y[1]-x[1];
  }
  export default {
    data() {
      return {
        trends: '',
        patient: this.$store.state.patient,
        data:'',
        update: true,
        mood_words: [],
      }
    },
    computed: {
      patient_id() {
        return this.$store.state.patient_id
      }
    },
    mounted() {
      const get_path = 'http://35.197.160.94:8094/patient/patientInfo?uidPatient=' + this.$store.state.patient_id
      axios.get(get_path)
      .then(response=>{
         this.trends=response.data;
         this.drawChart(this.trends);
         this.setMoodwords(this.trends);
      })
      .catch(error=>{
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })
    },
    watch: {
      patient_id(oldval, newval){
        const get_path = 'http://35.197.160.94:8094/patient/patientInfo?uidPatient=' + this.$store.state.patient_id
      axios.get(get_path)
      .then(response=>{
         this.trends=response.data;
         this.drawChart(this.trends);
         this.setMoodwords(this.trends);
      })
      .catch(error=>{
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })
        this.$forceUpdate();
      },
    },
    methods: {
      reload() {
        this.$forceUpdate()
      },
      setMoodwords(raw) {
        let mood_words = []
        raw.forEach(el => {
          if (el.emotion_client != null && el.share == 1) {
            const word_list = el.emotion_client.split(', ')
            word_list.forEach( ew => {
              if (ew != '') {
                const new_word = mood_words.find(e => e[0] === ew)
                if (!new_word) {
                  mood_words.push([ew,1])
                } else {
                  new_word[1]++
                }
              }
            })
          }
        })
        mood_words.sort(descend)
        this.mood_words = mood_words
      },
      drawChart(raw) {
        let date = []
        let index = 0
        let mood_list = []
        let entries = []
        raw.forEach(el => {
          if (el.timestamp != null && el.share == 1) {
            mood_list.push(el.mood)
            const value = el.timestamp.split('T')[0]
            const date = new Date(value)
            const day = value.split('-')[2]
            const year = value.split('-')[0]
            const mounth = date.toDateString().split(" ")[1]
            const format = day + ' ' + mounth + ' ' + year
            const new_day = entries.find(e => e[0] === format)
            if (!new_day) {
              entries.push([format,1])
            } else {
              new_day[1]++
            }
          }
        })
        const xindex = [...Array(index).keys()]
        let moodChart = this.$echarts.init(document.getElementById('MoodChart'))
        let entryChart = this.$echarts.init(document.getElementById('EntryChart'))
        moodChart.setOption({
          grid: {
            top: 40,
            left: 20,
            right: 20
          },
          xAxis: {
            show: false,
            data: xindex,
          },
          yAxis: {
            min: 0,
            max: 6,
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              show: false
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ['#35364B', '#686A7F', '#ACADB6', '#D5BB8F', '#2BDCB5', '#AADD01', '#FFA92B'],
                type: 'dashed'
              }
            }
          },
          series: [{
            data: mood_list,
            type: 'scatter',
            itemStyle: {
            color: function(params) {
                var colorList = [
                         '#35364B', '#686A7F', '#ACADB6', '#D5BB8F', '#2BDCB5', '#AADD01', '#FFA92B',
                        ];
                return colorList[params.value]
            }
        }
          }]
        })
        entryChart.setOption({
          xAxis: {
            type: 'category',
          },
          yAxis: {
            type: 'value',
            minInterval: 1,
          },
          series: [{
            data: entries,
            type: 'bar',
            itemStyle: {
              normal: {
                color: '#00577E'
              }
            }
          }]
        })
      }
    }
  }
</script>

